.wrapper1 {
    width: 600px;
    height: 300px;
    background-color: black;
    color: white;
    text-align: center;
    display: flex;
    align-items: center;
   
    position: absolute;
    left: -1600px;
    /*       */
    animation: slideLeft 1.5s ease-out forwards;
    /*  ,     */
    font-size: 40px;
}

.container {
    position: relative;
    overflow: hidden;
    /*        */
}

@keyframes slideLeft {
    0% {
        left: -1600px;
        /*       */
    }

    100% {
        left: -40px;
        /*  ,      */
    }
}

